
.header {
  border-top: 6px solid #e9792d;
  position: fixed;
  top: 0;
  z-index: 999;
  color: #e9792d;
  background-color: #fff;
  box-sizing: border-box;
  border-bottom: 1px solid #e5e5e5;
  -webkit-transition: 0.2s ease height;
  transition: 0.2s ease height;
}
.header .logo {
  margin-top: 29px;
  position: relative;
  width: 292px;
  float: left;
}
.header .logo:hover {
  opacity: 0.7;
}
.header .logo:before {
  content: '';
  position: absolute;
  top: -35px;
  height: 6px;
  width: 90%;
  background-color: #d91f17;
}
.header .business-data {
  margin-top: 24px;
  float: right;
  font-family: $roboto;
  text-align: justify;
  width: 63.2%;
}
.header .business-data:after {
  content: '';
  width: 100%;
  display: inline-block;
}
.header .block {
  vertical-align: middle;
  display: inline-block;
  font-size: 1em;
}
.header .block:first-of-type {
  margin-left: 0;
  float: left;
}
.header .block:last-of-type {
  margin-right: 0;
  float: right;
}
.header .block__data {
  color: #5d5d5d;
}
.header .block__data + .block__data {
  margin-top: 8px;
}
.header .block__data--styled {
  text-transform: uppercase;
  color: #999;
}
.header .block--time {
  color: #5d5d5d;
}
.header .block--time .block__date,
.header .block--time .block__time {
  font-size: 1.6em;
  font-family: Roboto-bold;
}
.header .block--time .block__divider {
  font-family: Roboto-thin;
}
.header .block--time .block__work-time {
  display: block;
  font-size: 0.9em;
  font-family: Roboto-light;
}
.header .block--phone {
  margin: 0 13%;
}
.header .block--email {
  width: 222px;
}
.header .navbar-container {
  padding: 16px 0;
  background-color: #3a3a3c;
}
.header .navbar-container .information {
  text-align: justify;
  width: 100%;
  vertical-align: middle;
  display: inline-block;
}
.header .navbar-container .information .return {
  margin-right: 190px;
  line-height: 46px;
  display: inline-block;
}
.header .navbar-container .information .return a {
  display: block;
  color: #e9792d;
  text-decoration: none;
  font-size: 1.2em;
  text-transform: uppercase;
  font-family: Roboto-bold;
}
.header .navbar-container .information .heading {
  vertical-align: middle;
  display: inline-block;
  font-family: Roboto-bold;
  font-size: 1.2em;
  text-transform: uppercase;
}
.header .navbar-container .information .heading__time,
.header .navbar-container .information .heading__date {
  margin-left: 5px;
  color: #fff;
}
.header .navbar-container .information .filter {
  display: inline-block;
  margin-left: 10px;
}
.header .navbar-container .information .filter .form-control select,
.header .navbar-container .information .navbar-wrapper .nav .navbar,
.header .navbar-container .information .navbar-wrapper .contacts {
  color: #5d5d5d;
  height: 46px;
  background: #fff url("../assets/images/dropdown.png") no-repeat 92% center;
  border-radius: 7px;
  padding: 0 36px 0 16px;
}
.header .navbar-container .information .navbar-wrapper {
  vertical-align: top;
  display: inline-block;
  text-align: justify;
  width: 58.5%;
}
.header .navbar-container .information .navbar-wrapper .nav {
  display: inline-block;
  position: relative;
  width: 150px;
  margin-left: 40px;
  height: 46px;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar,
.header .navbar-container .information .navbar-wrapper .contacts {
  vertical-align: top;
  padding: 0;
  display: inline-block;
  background: #3a3a3c url("../assets/images/dropdown--white.png") no-repeat 92% center;
  border: 1px solid rgba(255,255,255,0.31);
  text-transform: uppercase;
  color: #fff;
  font-family: Roboto-bold;
  font-size: 1em;
  cursor: pointer;
  position: absolute;
  top: 0;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar--active,
.header .navbar-container .information .navbar-wrapper .contacts--active {
  height: 135px;
  background: #3a3a3c url("../assets/images/dropdown--white.png") no-repeat 92% 20px;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar__link,
.header .navbar-container .information .navbar-wrapper .contacts__link {
  display: block;
  line-height: 45px;
  color: #fff;
  text-decoration: none;
  position: absolute;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar__link:nth-child(2),
.header .navbar-container .information .navbar-wrapper .contacts__link:nth-child(2) {
  top: 30px;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar__link:nth-child(3),
.header .navbar-container .information .navbar-wrapper .contacts__link:nth-child(3) {
  top: 60px;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar__link:nth-child(4),
.header .navbar-container .information .navbar-wrapper .contacts__link:nth-child(4) {
  top: 90px;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar__link--void,
.header .navbar-container .information .navbar-wrapper .contacts__link--void {
  position: relative;
  font-size: 1em !important;
  margin: 0 36px 0 16px !important;
  line-height: 44px;
  z-index: value;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar__link--void:before,
.header .navbar-container .information .navbar-wrapper .contacts__link--void:before {
  position: absolute;
  content: '';
  left: -25px;
  top: 15px;
  width: 15px;
  height: 15px;
  background: #fff url("../assets/images/i.png") no-repeat center center;
  background-size: cover;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar__link--void:hover,
.header .navbar-container .information .navbar-wrapper .contacts__link--void:hover {
  color: #fff !important;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar__link--active,
.header .navbar-container .information .navbar-wrapper .contacts__link--active {
  margin-left: 16px;
  font-size: 0.8em;
}
.header .navbar-container .information .navbar-wrapper .nav .navbar__link--active:hover,
.header .navbar-container .information .navbar-wrapper .contacts__link--active:hover {
  color: #e9792d;
}
.header .navbar-container .information .navbar-wrapper .contacts {
  margin-left: 46px;
  width: 150px;
  line-height: 46px;
  color: #fff;
  background: #3a3a3c;
  position: static;
}
.header .navbar-container .information .navbar-wrapper .contacts__link {
  width: 100%;
  padding: 0 36px 0 16px;
  font-size: 1em;
}
.header .navbar-container .information .navbar-wrapper .contacts__link:before {
  position: absolute;
  content: '';
  left: -8px;
  top: 12px;
  width: 15px;
  height: 20px;
  background: #fff url("../assets/images/pin.png") no-repeat center center;
  background-size: cover;
}
.header .navbar-container .information .navbar-wrapper .calc {
  cursor: pointer;
  float: right;
  vertical-align: middle;
  display: inline-block;
  vertical-align: top;
  position: relative;
}
.header .navbar-container .information .navbar-wrapper .calc:before {
  position: absolute;
}
.header .navbar-container .information .navbar-wrapper .calc a {
  padding: 0 16px 0 16px;
  line-height: 50px;
  height: 46px;
  width: 222px;
  display: inline-block;
  border-radius: 7px;
  text-align: center;
  background: #d91f17;
  text-decoration: none;
  font-family: Roboto-black;
  font-size: 0.9em;
  color: #fff;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}
.header .navbar-container .information .navbar-wrapper .calc a span {
  display: inline-block;
}
.header .navbar-container .information .navbar-wrapper .calc a span + span {
  margin-left: 20px;
}
.header .navbar-container .information .navbar-wrapper .calc a:hover {
  color: #d91f17;
  background-color: #fff;
}


