@font-face {
  font-family: 'icomoon';
  src:  url('./fonts/icomoon.eot?cb9r37');
  src:  url('./fonts/icomoon.eot?cb9r37#iefix') format('embedded-opentype'),
    url('./fonts/icomoon.ttf?cb9r37') format('truetype'),
    url('./fonts/icomoon.woff?cb9r37') format('woff'),
    url('./fonts/icomoon.svg?cb9r37#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-calc:before {
  content: "\e900";
}
.icon-map:before {
  content: "\e901";
}




