
.calculator {
  width: 300px;
  border-radius: 7px;
  background-color: #fff;
  height: 487px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  padding: 30px 43px;
  font-family: Roboto-bold;
}
.calculator__close {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 40px;
  font-size: 20px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  color: #e9792d;
}
.calculator__close:hover {
  color: #d91f17;
}
.calculator .title {
  color: #3a3a3c;
  font-size: 2em;
}


