
.item {
  padding: 26px 0;
  border-bottom: 1px solid #f4924f;
  background-color: #f17d2f;
}
.item:first-child {
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}
.item:last-child {
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}
.item:hover {
  background-color: #dc6d22;
}
.item .block {
  display: inline-block;
  color: #fff;
  font-family: Roboto-black;
}
.item .block--logo {
  margin: 0 21px;
}
.item .block--logo img {
  display: block;
}
.item .block--currency {
  width: 15%;
  font-size: 1.9em;
}
.item .block--buy {
  margin-left: 45px;
  border-right: 1px solid #f4924f;
  padding: 0 35px;
}
.item .block--sell {
  padding: 0 35px;
}
.item .block--buy,
.item .block--sell {
  width: 25%;
  font-size: 2em;
}
.item .block--buy .block__price,
.item .block--sell .block__price {
  display: inline-block;
  float: left;
  text-align: right;
}
.item .block--buy .block__delta,
.item .block--sell .block__delta {
  display: inline-block;
  float: right;
  vertical-align: middle;
  padding-left: 25px;
  font-size: 0.5em;
  font-family: Roboto-thin;
  position: relative;
  margin: 10px 0 0 12px;
  text-align: center;
  width: 57px;
}
.item .block--buy .block__delta:before,
.item .block--sell .block__delta:before {
  position: absolute;
  padding: 1px;
  width: 20px;
  height: 20px;
  top: -3px;
  left: 0px;
  border-radius: 50%;
  text-align: center;
}
.item .block--buy .block__delta--up:before,
.item .block--sell .block__delta--up:before {
  line-height: 20px;
  content: '\02C4';
  background-color: #d91f17;
}
.item .block--buy .block__delta--down:before,
.item .block--sell .block__delta--down:before {
  line-height: 22px;
  content: '\02C5';
  background-color: #0ab11d;
}
.item .block--reserve {
  background: #fff;
  border-radius: 7px;
  padding: 0 18px;
  color: #e9792d;
  font-size: 1em;
  font-family: Roboto-black;
  display: inline-block;
  height: 46px;
  font-size: 0.9em;
  vertical-align: middle;
  line-height: 46px;
  float: right;
  cursor: pointer;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
  margin: -5px 20px 0 0;
}
.item .block--reserve:hover {
  color: #fff;
  background-color: #d91f17;
}


